/* Body
-------------------------------------------------- */
body {
  position: relative;
  background-color: #fff;
}
.skeleton {
  border-right: 1px solid #eee;
  border-left: 1px solid #eee;
  background-color: #fff;
}
/* Cover Image
-------------------------------------------------- */
.cover {
  display: none;
}
.cover.cover-init {
  display: block;
}
.cover-figure-img {
  display: block;
  max-width: 100%;
  height: auto;
}
.cover-figcaption {
  margin: 0;
  padding: 0;
  font-size: 0;
  display: none;
}
.cover_carousel {
  position: relative;
}
.cover_carousel .carousel-inner {
  position: relative;
  overflow: hidden;
  width: 100%;
}
.cover_carousel .carousel-inner > .item {
  display: none;
  position: relative;
  -webkit-transition: 0.6s ease-in-out left;
  transition: 0.6s ease-in-out left;
}
.cover_carousel .carousel-inner > .item > img,
.cover_carousel .carousel-inner > .item > a > img {
  display: block;
  max-width: 100%;
  height: auto;
  line-height: 1;
}
.cover_carousel .carousel-inner > .active,
.cover_carousel .carousel-inner > .next,
.cover_carousel .carousel-inner > .prev {
  display: block;
}
.cover_carousel .carousel-inner > .active {
  left: 0;
}
.cover_carousel .carousel-inner > .next,
.cover_carousel .carousel-inner > .prev {
  position: absolute;
  top: 0;
  width: 100%;
}
.cover_carousel .carousel-inner > .next {
  left: 100%;
}
.cover_carousel .carousel-inner > .prev {
  left: -100%;
}
.cover_carousel .carousel-inner > .next.left,
.cover_carousel .carousel-inner > .prev.right {
  left: 0;
}
.cover_carousel .carousel-inner > .active.left {
  left: -100%;
}
.cover_carousel .carousel-inner > .active.right {
  left: 100%;
}
.cover_carousel .carousel-indicators {
  position: absolute;
  left: 0;
  bottom: 20px;
  padding: 0;
  margin: 0 20px;
  z-index: 15;
  text-align: left;
  list-style: none;
}
.cover_carousel .carousel-indicators li {
  display: inline-block;
  width: 10px;
  height: 10px;
  cursor: pointer;
  background-color: #000;
  border-radius: 10px;
}
.cover_carousel .carousel-indicators .active {
  background-color: #00a1db;
}
/* Pages
-------------------------------------------------- */
.search,
.localog,
.taglog,
.medialog {
  padding-bottom: 20px;
  border-bottom: 1px solid #ccc;
}
.search h1,
.localog h1,
.taglog h1,
.medialog h1 {
  position: relative;
  margin-top: 15px;
  margin-bottom: 15px;
  font-weight: bold;
  -webkit-font-smoothing: antialiased;
  cursor: text;
  padding-left: 15px;
  padding-right: 15px;
  padding-bottom: 13px;
  font-size: 22px;
  border-bottom: 1px solid #eee;
  color: black;
}
.search ul,
.localog ul,
.taglog ul,
.medialog ul,
.search ol,
.localog ol,
.taglog ol,
.medialog ol {
  margin: 0 15px;
  padding: 0;
  list-style-position: inside;
}
.taglog li {
  display: inline;
}
/* Content
-------------------------------------------------- */
.hentry {
  background-color: #fff;
  border-right: 1px solid #eee;
  border-bottom: 1px solid #eee;
}
.entry-header {
  margin-bottom: 20px;
  border-bottom: 1px solid #ccc;
}
.entry-title {
  margin: 0!important;
  padding: 15px!important;
  font-size: 22px!important;
  line-height: 1.4!important;
  font-weight: 900;
  color: #000;
}
.entry-title a {
  color: #000;
  padding-bottom: 2px;
  text-decoration: none;
}
.entry-title a:hover,
.entry-title a:focus {
  padding-bottom: 2px;
  border-bottom: 1px solid #00a1db;
  color: #00a1db;
  text-decoration: none;
}
.byline {
  line-height: 1.5;
  color: #555;
}
.updated {
  font-size: 13px;
  color: #999;
}
.entry-content {
  padding-bottom: 40px;
}
.entry-content #marked {
  display: none!important;
}
