  width: 100% !important;
}
.entry-content .another_category table th {
  padding: 10px !important;
  text-align: left;
  font-weight: normal;
  letter-spacing: 0px;
}
.entry-content .another_category table th a {
  color: #888;
  font-size: 13px;
}
.entry-content .another_category table td {
  padding: 5px 10px 5px 0 !important;
  font-size: 11px;
  text-align: right;
  color: #888;
}
.entry-tags {
  margin: 10px 0;
  padding: 0 15px 0 20px;
  min-height: 25px;
  color: #fff;
}
.taglog li a,
.entry-tags a {
  position: relative;
  display: inline-block;
  padding: 0 6px 0 8px;
  margin: 4px 12px 4px 5px;
  color: #222;
  font-size: 12px;
  background-color: #eee;
  border-radius: 0 2px 2px 0;
  text-decoration: none;
}
.taglog li a:hover,
.entry-tags a:hover {
  color: #FFF;
  background-color: #00a1db;
}
.taglog li a:after,
.entry-tags a:after {
  content: "";
  position: absolute;
  border: 2px solid #FFFFFF;
  top: 10px;
  left: -1px;
  border-radius: 50%;
}
.taglog li a:before,
.entry-tags a:before {
  border: solid transparent;
  content: "";
  height: 0;
  right: 100%;
  position: absolute;
  width: 0;
  border-width: 12px;
  border-right-color: #eee;
  top: 0px;
}
.taglog li a:hover:before,
.entry-tags a:hover:before {
  border: solid transparent;
  content: "";
  height: 0;
  right: 100%;
  position: absolute;
  width: 0;
  border-width: 12px;
  border-right-color: #00a1db;
  top: 0px;
