/* Small devices < 768px
------------------------- */
@media (max-width: 767px) {
  .skeleton,
  .hentry {
    border: none;
  }
  .cover {
    margin-bottom: 30px;
  }
  .cover_carousel .carousel-indicators {
    display: block;
    position: absolute;
    bottom: -30px;
    left: 0;
    width: 100%;
    margin: 0 auto;
    text-align: center;
  }
  .footer {
    padding: 20px 15px;
  }
  .footer li {
    display: block;
    font-size: 10px;
  }
  /* Remove hover
  ------------------------- */
  .widget li a:hover {
    color: #555;
  }
  .js-widget-feed h4 a:hover {
    color: #333;
    border-bottom: 0;
  }
  .js-widget-feed P a:hover {
    color: #999;
  }
  .media-thumbnail:hover {
    box-shadow: 0px 0px 7px #A0A0A0;
  }
  .tt-item a:hover {
    color: #888!important;
  }
  .tt-post-thumb:hover {
    box-shadow: 0px 0px 7px #A0A0A0;
  }
}
